*, *:before, *:after {
  box-sizing: border-box;
}

html, body, div, span, object, iframe, figure, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, code, em, img, small, strike, strong, sub, sup, tt, b, u, i, ol, ul, li, fieldset, form, label, table, caption, tbody, tfoot, thead, tr, th, td, main, canvas, embed, footer, header, nav, section, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  text-size-adjust: none;
}

footer, header, nav, section, main {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input {
  border-radius: 0;
}

a {
  font-family: "Roboto";
  text-transform: uppercase;
  text-decoration: none;
}

h1 {
  font-family: "Inter";
  font-size: clamp(2rem, 4vw, 3rem);
  line-height: 1.5em;
  font-weight: 700;
  color: #4F4F4F;
  margin: 1rem 0rem;
}

h2 {
  font-family: "Inter";
  color: #333333;
  font-size: clamp(1.825rem, 3vw, 2.1875rem);
  line-height: 1.2em;
  font-weight: 400;
}

h3 {
  font-family: "Inter";
  color: #333333;
  font-size: 1.5rem;
  font-size: clamp(1.25rem, 2vw, 1.5rem);
  line-height: 1.2em;
  font-weight: 400;
}

p {
  font-family: "Inter";
  font-weight: 400;
  font-size: 1.5rem;
  font-size: clamp(1rem, 1.5vw, 1.5rem);
  line-height: 1.5em;
  color: #5A6B7B;
  margin: 1rem 0rem;
}

select {
  font-family: "Roboto";
  font-size: 1rem;
  font-weight: 500;
  line-height: 1.2em;
}

.container {
  max-width: 1195px;
  margin: 0 auto;
  display: flex;
  padding: 2rem 0rem;
}
@media (max-width: 1280px) {
  .container {
    padding: 1rem 2rem;
  }
}
.button {
  display: inline-block;
  border-radius: 5px;
}

.button-primary {
  color: #fff;
  font-size: 0.9375rem;
  line-height: 1.375rem;
  padding: 1rem 2rem;
  background-color: #0288D1;
  border-bottom: 4px solid #0277BD;
  transition: all 0.3s ease-in;
}
@media (max-width: 768px) {
  .button-primary {
    padding: 0.5rem 1.5rem;
  }
}
.button-primary:hover {
  background-color: #09a7fd;
}

.navbar {
  position: fixed;
  width: 100%;
  transition: all 0.2s ease-in;
}
.navbar.scrolled {
  background: #fff;
  border-bottom: 1px solid #03A9F4;
}
.navbar.scrolled .container {
  padding-top: 1rem;
  padding-bottom: 1rem;
}
.navbar .container {
  justify-content: space-between;
  height: 100%;
  transition: all 0.2s ease-in;
}
.navbar .nav-logo {
  margin-right: 3rem;
}
.navbar .nav-menu {
  display: flex;
  flex: 1;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.navbar .nav-menu__left a:last-child, .navbar .nav-menu__right a:last-child {
  margin-right: 0;
}
.navbar .nav-menu a {
  text-decoration: none;
  color: #5A6B7B;
  text-transform: uppercase;
  margin: 0 1.5rem;
  font-size: 0.8125rem;
}
.navbar .nav-menu a:hover {
  color: #718597;
}
.navbar .nav-menu .btn {
  border: 1px solid #03A9F4;
  padding: 0.8125rem 2.5rem;
  color: #03A9F4;
  border-radius: 3px;
}
.navbar .nav-menu .btn:hover {
  color: #2ebcfc;
  border-color: #2ebcfc;
}

select {
  padding: 1rem 1.875rem;
  width: 17.8125rem;
  border-radius: 5px;
  background: #EFF4F7;
  -webkit-appearance: none;
  border: 1px solid #C6D2D9;
}

.hero {
  min-height: 43.75rem;
  background: linear-gradient(#EAF8FF, #fff);
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 100px;
}
@media (max-width: 1280px) {
  .hero {
    min-height: 75vh;
  }
}
@media (max-width: 768px) {
  .hero {
    flex-direction: column;
  }
}
.hero__left--out {
  width: calc((100vw - 1195px) / 2);
}
.hero__left--in {
  flex: 1.2;
}
.hero__right--in {
  flex: 2;
  display: flex;
  justify-content: flex-end;
}
@media (max-width: 1280px) {
  .hero__left--out {
    display: none;
  }
  .hero__left--in, .hero__right--in {
    flex: 1;
    width: 50%;
  }
  .hero__left--in {
    padding: 2rem;
  }
  .hero__right--in img {
    width: 100%;
  }
}
@media (max-width: 768px) {
  .hero__left--in, .hero__right--in {
    width: 100%;
  }
}
.hero__review {
  display: flex;
  align-items: center;
  gap: 1rem;
}
@media (max-width: 480px) {
  .hero__review {
    flex-direction: column;
    align-items: flex-start;
  }
}
.hero__review span {
  font-family: "Inter";
  font-size: 1.125rem;
  color: #546E7A;
}
@media (max-width: 768px) {
  .hero__review span {
    font-size: 1rem;
  }
}
.hero__text {
  margin-bottom: 2rem;
}

.trusted__text {
  font-size: 1.125rem;
  line-height: 1.625rem;
  color: #7F9CAD;
  display: flex;
  justify-content: center;
  width: 100%;
  text-align: center;
  position: relative;
}
.trusted__text:before, .trusted__text:after {
  content: "";
  flex: 1;
  height: 0;
  border-top: solid rgba(127, 156, 173, 0.3) 1px;
}
.trusted__text:before {
  margin: auto 1em auto auto;
}
.trusted__text:after {
  margin: auto auto auto 1em;
}
@media (max-width: 480px) {
  .trusted__text {
    padding: 2rem 0;
  }
  .trusted__text:before, .trusted__text:after {
    margin: 0;
    position: absolute;
    height: 1px;
    width: 70%;
    display: block;
  }
  .trusted__text:after {
    top: 0;
    left: 0;
  }
  .trusted__text:before {
    bottom: 0;
    right: 0;
  }
}

.icons .container {
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 50px;
}
@media (max-width: 768px) {
  .icons .container {
    justify-content: center;
  }
}
.icons__item {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
}
@media (max-width: 480px) {
  .icons__item {
    width: 100%;
  }
}
.icons__item span {
  font-family: Roboto;
  font-size: 2.75rem;
  font-size: clamp(1.825rem, 3vw, 2.75rem);
  line-height: 1.4em;
  color: #3D4853;
}

.features .container {
  flex-direction: column;
}
.features__heading {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1.25rem;
  align-items: center;
}
@media (max-width: 480px) {
  .features__heading {
    flex-direction: column;
    gap: 2rem;
  }
}
.features__list {
  display: flex;
  flex-direction: column;
}
.features__list .features__item {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid #C6D2D9;
}
.features__list .item_info h3 {
  margin-top: 1.25rem;
}
.features__list .item_info p {
  font-family: Roboto;
  text-transform: uppercase;
  font-size: 0.875rem;
  line-height: 1.2em;
  color: #3D4853;
  margin-top: 0.625rem;
  margin-bottom: 1.25rem;
}

.navbar .menu-icon {
  position: relative;
  cursor: pointer;
  z-index: 1;
  display: none;
}
@media (max-width: 768px) {
  .navbar .menu-icon {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
.navbar .menu-icon__line {
  display: block;
  position: relative;
  background: #03a9f4;
  height: 2px;
  width: 20px;
  border-radius: 5px;
  transition: background 0.8s ease-in-out;
}
.navbar .menu-icon__line::before, .navbar .menu-icon__line::after {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  border-radius: 5px;
  background: #03a9f4;
  transition: background 0.8s ease-in-out;
}
.navbar .menu-icon__line::before {
  transform: translateY(-5px);
}
.navbar .menu-icon__line::after {
  transform: translateY(5px);
}
@media (max-width: 768px) {
  .navbar .nav-menu {
    position: absolute;
    top: 0;
    right: -10rem;
    opacity: 0;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10rem 0;
    width: 0vw;
    height: 100vh;
    background-color: #718597;
    color: #fff;
    transition: all 0.5s ease;
  }
}
@media (max-width: 768px) {
  .navbar .nav-menu__left, .navbar .nav-menu__right {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
  }
  .navbar .nav-menu__left a, .navbar .nav-menu__right a {
    color: #fff;
    margin: 0;
    padding: 1.5rem;
    font-weight: bold;
    transition: all 0.3s ease;
  }
  .navbar .nav-menu__left a:hover, .navbar .nav-menu__right a:hover {
    color: #2ebcfc;
  }
  .navbar .nav-menu__left a.btn, .navbar .nav-menu__right a.btn {
    color: #fff;
    border-color: #fff;
  }
}
.navbar .menu-btn {
  display: none;
}
@media (max-width: 768px) {
  .navbar .menu-btn:checked ~ .nav-menu {
    right: 0;
    opacity: 1;
    width: 40vw;
  }
}
@media (max-width: 480px) {
  .navbar .menu-btn:checked ~ .nav-menu {
    width: 100vw;
  }
}
@media (max-width: 768px) {
  .navbar .menu-btn:checked ~ .menu-icon .menu-icon__line,
  .navbar .menu-btn:checked ~ .menu-icon .menu-icon__line::after,
  .navbar .menu-btn:checked ~ .menu-icon .menu-icon__line::before {
    background: #fff;
  }
}